$tablet: '(min-width: 768px)';
$laptop: '(min-width: 1024px)';
$desktop: '(min-width: 1280px)';

$yellow: #ffca41;
$orange: #e05e31;
$green: #3ca974;
$text-color: #000;
$text-color-medium: #333;
$text-color-light: rgba($text-color, 0.5);
$text-color-white: #fff;
$grey: #94969d;
$light-grey: #f5f5f5;

$accent-color: red;

@mixin container {
    position: relative;

    width: 100%;
    margin: 0 auto;
    min-width: 320px;
    max-width: 1306px;
    padding-left: 20px;
    padding-right: 20px;

    @media #{$tablet} {
        padding-left: 80px;
        padding-right: 80px;
    }
}

@mixin btn-reset {
    padding: 0;
    margin: 0;
    background: none;
    border: none;
    outline: none;
}

@mixin visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

@mixin svg-icon {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    pointer-events: none;
}

@mixin img {
    display: block;
    width: 100%;
    height: auto;
    max-width: 100%;
    max-height: 100%;
}

@mixin hideScrollbar() {
    &::-webkit-scrollbar {
        display: none;
    }
    -ms-overflow-style: none;
    scrollbar-width: none;
}
